  logout() {
    localStorage.removeItem('token');
    localStorage.removeItem('RoleID');
    localStorage.removeItem('UserId');
    localStorage.removeItem('LoginUser');
    this.alertify.message('Logged out');
    this.router.navigate(['/login']);
 }
